import { EditorView, basicSetup } from "codemirror";
import { LRLanguage, LanguageSupport, syntaxTree } from "@codemirror/language";
import { linter, Diagnostic } from "@codemirror/lint";

import { parser } from "./concrete.grammar";
import { inferTerm, TypeInferenceError } from "./inference.ts";
import { Term, Type, printType, printTerm } from "./ast.ts";
import { styleTags, tags as t, highlightCode, classHighlighter } from "@lezer/highlight";
import { interpret } from "./interpreter.ts";

const strictParser = parser.configure({ strict: true });

export function parseAndInfer(source: string) {
  const cst = strictParser.parse(source);
  console.log(cst.toString());
  const { term, type } = inferTerm(cst.topNode.getChild("Term")!, {
    source,
    context: [],
    level: 0,
    isPattern: false,
  });
  return { term, type };
}

const highlightParser = parser.configure({
  props: [
    styleTags({
      VariableName: t.variableName,
      Level: t.number,
      "with if then else fun wrap let$ match$ let rec in rewrite": t.keyword,
      "true false": t.bool,
      "int bool code": t.typeName,
      "<|": t.typeOperator,
      "LitIntTerm LitIntPattern": t.number,
      "( )": t.paren,
      "[ ]": t.squareBracket,
      "< >": t.angleBracket,
      QuoteTerm: t.quote,
    }),
  ],
});

const highlightTypeParser = highlightParser.configure({ top: "TypeOnly" });

const concreteLanguage = LRLanguage.define({ parser: highlightParser });

function concreteLanguageSupport(resultEl: HTMLElement): LanguageSupport {
  return new LanguageSupport(concreteLanguage, [
    linter((view) => {
      // Clear previous diagnostics
      resultEl.replaceChildren();

      const diagnostics: Diagnostic[] = [];
      const tree = syntaxTree(view.state);
      tree.iterate({
        enter: (node) => {
          if (node.type.isError) {
            diagnostics.push({
              from: node.from,
              to: node.to,
              severity: "error",
              message: "syntax error",
            });
          }
        },
      });

      if (diagnostics.length != 0) {
        resultEl.appendChild(details("Syntax Error", "There is a syntax error in your code.", { open: true }));
        return diagnostics;
      }

      try {
        const { term, type } = inferTerm(tree.topNode.getChild("Term")!, {
          source: view.state.sliceDoc(),
          context: [],
          level: 0,
          isPattern: false,
        });

        resultEl.appendChild(details("Inferred Type", printHighlightedType(type), { open: true }));

        resultEl.appendChild(details("Inferred Term", printHighlightedTerm(term), { open: false }));

        let currentStage = 0;
        let currentTerm = term;
        while (true) {
          const result = interpret(currentStage, currentTerm as Term, [], 0);
          resultEl.appendChild(details(`Stage ${currentStage} Result`, printHighlightedTerm(result.term), { open: true }));
          if (result.term.t !== "quote") {
            break;
          }
          currentStage += 1;
          currentTerm = result.term.term;
        }
      } catch (error) {
        if (error instanceof TypeInferenceError) {
          diagnostics.push({
            from: error.node.from,
            to: error.node.to,
            severity: "error",
            message: error.message,
          });
          resultEl.appendChild(details("Type Error", error.message, { open: true }));
        } else {
          resultEl.appendChild(details("Runtime Error", error.message, { open: true }));
        }
        console.error(error);
      }

      return diagnostics;
    }),
  ]);
}

function details(summary: string, content: string | Node, { open = false }: { open: boolean }): HTMLElement {
  const detailsEl = document.createElement("details");
  const summaryEl = document.createElement("summary");
  summaryEl.appendChild(document.createTextNode(summary));
  detailsEl.appendChild(summaryEl);
  const contentEl = document.createElement("pre");
  contentEl.style.textWrap = "auto";
  if (typeof content === "string") {
    contentEl.appendChild(document.createTextNode(content));
  } else {
    contentEl.appendChild(content);
  }
  detailsEl.appendChild(contentEl);
  detailsEl.open = open;
  return detailsEl;
}

const examples = {
  stagedPowerFunction: `let rec power : int code -> int -> int code = fun x -> fun n ->
  if n == 0 then <1>
  else
    let$ s1 = x in
    let$ s2 = power x (n - 1) in
    <s1 * s2> in
let$ power5 [x : int] = power <x> 5 in
  <fun x : int -> power5>`,

  anaphoricIf: `let aif : bool code -> int code [it : bool] -> int code [it : bool] -> int code = 
  fun cond -> fun then_branch -> fun else_branch ->
    let$ s1 = cond in
    let$ s2 [it : bool] = then_branch in
    let$ s3 [it : bool] = else_branch in
    <let it = s1 in if it then s2 else s3>
in
  aif <true> <42> <43>`,

  symbolicDifferentiation: `let rec partial : int code -> int code [var: int] = fun e ->
  match$ e with
    | \`var -> <1>
    | (g + h) -> let$ dg = partial <g> in let$ dh = partial <h> in <dg + dh>
    | (g * h) -> let$ dg = partial <g> in let$ dh = partial <h> in <dg * h + g * dh>
    | (let y : int = f in g) ->
      let$ df = partial <f> in
      let$ dg1 [y: int] = partial <g> in
      let$ dg2 [y: int] = partial[var=y] <g> in
      <let y = f in dg1 + dg2 * df>
    | _ -> <0>
in
  wrap [x : int; y : int]
    partial [var = x] <x * y + 1>
      rewrite (z * 0) -> <0>
      rewrite (z + 0) -> <z>
      rewrite (1 * z) -> <z>`
}


let editor = new EditorView({
  extensions: [basicSetup, concreteLanguageSupport(document.getElementById("result")!)],
  parent: document.getElementById("editor")!,
  doc: examples.symbolicDifferentiation
});

document.querySelectorAll<HTMLElement>("#playground-examples a").forEach((el) => {
  el.addEventListener("click", (event) => {
    event.preventDefault();
    const exampleKey = el.dataset.exampleKey;
    if (exampleKey && examples[exampleKey]) {
      editor.dispatch({ changes: { from: 0, to: editor.state.doc.length, insert: examples[exampleKey] } });
    }
  });
});

for (const el of document.querySelectorAll<HTMLElement>(".example")) {
  const editorEl = el.querySelector<HTMLElement>(".editor")!;
  const resultEl = el.querySelector<HTMLElement>(".result")!;
  const exampleEditor = new EditorView({
    extensions: [basicSetup, concreteLanguageSupport(resultEl)],
    doc: editorEl.innerText
  });
  editorEl.replaceWith(exampleEditor.dom);
}

function printHighlightedType(type: Type): DocumentFragment {
  const source = printType(type);
  const tree = highlightTypeParser.parse(source);
  const frag = document.createDocumentFragment();
  highlightCode(source, tree, classHighlighter, (code: string, classes: string) => {
    let node: Node = document.createTextNode(code);
    if (classes) {
      const span = document.createElement("span");
      span.className = classes;
      span.appendChild(node);
      node = span;
    }
    frag.appendChild(node);
  }, () => { frag.appendChild(document.createTextNode("\n")) });
  return frag;
}

function printHighlightedTerm(term: Term): DocumentFragment {
  const source = printTerm(term);
  const tree = highlightParser.parse(source);
  const frag = document.createDocumentFragment();
  highlightCode(source, tree, classHighlighter, (code: string, classes: string) => {
    let node: Node = document.createTextNode(code);
    if (classes) {
      const span = document.createElement("span");
      span.className = classes;
      span.appendChild(node);
      node = span;
    }
    frag.appendChild(node);
  }, () => { frag.appendChild(document.createTextNode("\n")) });
  return frag;
}

