export type Name = string;
export type Ix = number; // Debruijn index

export type Type =
  | { t: "bool" }
  | { t: "int" }
  | { t: "code"; type: Type }
  | { t: "fun"; arg: Type; deps: Context; ret: Type }
  | { t: "wrap"; deps: Context; type: Type };

// patterns are terms extended pattern variables
type PatternExt =
  | { t: "patternVariable"; bind: ContextItem }
  | { t: "wildcard" };

export type TermF<E extends object> =
  | E // Additional cases
  | { t: "varSubst"; ix: Ix; subst: SubstF<E> }
  | { t: "true" }
  | { t: "false" }
  | { t: "if"; cond: TermF<E>; then: TermF<E>; else: TermF<E> }
  | { t: "ctxAbs"; bind: ContextItem; body: TermF<E> }
  | { t: "ctxApp"; fun: TermF<E>; deps: Context; arg: TermF<E> }
  | { t: "quote"; term: TermF<E> }
  | { t: "letQuote"; bind: ContextItem; def: TermF<E>; body: TermF<E> }
  | { t: "wrap"; deps: Context; term: TermF<E> }
  | { t: "letWrap"; bind: ContextItem; def: TermF<E>; body: TermF<E> }
  | { t: "match"; target: TermF<E>; branches: MatchBranchF<E>[] }
  | { t: "letRec"; bind: ContextItem; def: TermF<E>; body: TermF<E> }
  | { t: "arith"; op: "+" | "-" | "*" | "/"; left: TermF<E>; right: TermF<E> }
  | { t: "eq"; left: TermF<E>; right: TermF<E> }
  | { t: "litInt"; value: number }
  | {
      t: "rewrite";
      target: TermF<E>;
      pattern: TermF<E | PatternExt>;
      patternVariables: Context;
      replacement: TermF<E>;
    };

export type MatchBranchF<E extends object> = {
  pattern: TermF<E | PatternExt>;
  patternVariables: Context;
  body: TermF<E>;
};

export type SubstF<E extends object> = SubstItemF<E>[];
export type SubstItemF<E extends object> =
  | Ix
  | { term: TermF<E>; level: number; deps: Context };

export type Term = TermF<never>;
export type MatchBranch = MatchBranchF<never>;
export type Subst = SubstF<never>;
export type SubstItem = SubstItemF<never>;

export type Pattern = TermF<PatternExt>;
export type MatchBranchPattern = MatchBranchF<PatternExt>;
export type SubstPattern = SubstF<PatternExt>;
export type SubstItemPattern = SubstItemF<PatternExt>;

export type Context = ContextItem[];
export type ContextItem = {
  name: Name;
  level: number;
  type: Type;
  deps: Context;
};

export function isEqualType(a: Type, b: Type): boolean {
  if (a.t == "bool" && b.t == "bool") return true;
  if (a.t == "int" && b.t == "int") return true;
  if (a.t == "code" && b.t == "code") return isEqualType(a.type, b.type);
  if (a.t == "fun" && b.t == "fun")
    return (
      isEqualType(a.arg, b.arg) &&
      isEqualContext(a.deps, b.deps) &&
      isEqualType(a.ret, b.ret)
    );
  if (a.t == "wrap" && b.t == "wrap")
    return isEqualContext(a.deps, b.deps) && isEqualType(a.type, b.type);
  return false;
}

export function isEqualTerm(a: Term | Pattern, b: Term | Pattern): boolean {
  if (a.t === "patternVariable" && b.t === "patternVariable") {
    return true; // TODO: should we compare names?
  }
  if (a.t === "wildcard" && b.t === "wildcard") {
    return true; // Wildcards are always equal
  }
  if (a.t === "varSubst" && b.t === "varSubst")
    return a.ix === b.ix && isEqualSubst(a.subst, b.subst);
  if (a.t === "true" && b.t === "true") return true;
  if (a.t === "false" && b.t === "false") return true;
  if (a.t === "if" && b.t === "if") {
    return (
      isEqualTerm(a.cond, b.cond) &&
      isEqualTerm(a.then, b.then) &&
      isEqualTerm(a.else, b.else)
    );
  }
  if (a.t === "ctxAbs" && b.t === "ctxAbs") {
    return isEqualContextItem(a.bind, b.bind) && isEqualTerm(a.body, b.body);
  }
  if (a.t === "ctxApp" && b.t === "ctxApp") {
    return isEqualTerm(a.fun, b.fun) && isEqualTerm(a.arg, b.arg);
  }
  if (a.t === "quote" && b.t === "quote") return isEqualTerm(a.term, b.term);
  if (a.t === "letQuote" && b.t === "letQuote") {
    return (
      isEqualContextItem(a.bind, b.bind) &&
      isEqualTerm(a.def, b.def) &&
      isEqualTerm(a.body, b.body)
    );
  }
  if (a.t === "wrap" && b.t === "wrap") {
    return isEqualTerm(a.term, b.term) && isEqualContext(a.deps, b.deps);
  }
  if (a.t === "letWrap" && b.t === "letWrap") {
    return (
      isEqualContextItem(a.bind, b.bind) &&
      isEqualTerm(a.def, b.def) &&
      isEqualTerm(a.body, b.body)
    );
  }
  if (a.t === "match" && b.t === "match") {
    if (a.branches.length !== b.branches.length) return false;
    if (!isEqualTerm(a.target, b.target)) return false;
    for (let i = 0; i < a.branches.length; i++) {
      const ab = a.branches[i];
      const bb = b.branches[i];
      if (
        !isEqualTerm(ab.pattern, bb.pattern) ||
        !isEqualTerm(ab.body, bb.body)
      ) {
        return false;
      }
    }
    return true;
  }
  if (a.t === "letRec" && b.t === "letRec") {
    return (
      isEqualContextItem(a.bind, b.bind) &&
      isEqualTerm(a.def, b.def) &&
      isEqualTerm(a.body, b.body)
    );
  }
  if (a.t === "arith" && b.t === "arith") {
    return a.op == b.op && isEqualTerm(a.left, b.left) && isEqualTerm(a.right, b.right);
  }
  if (a.t === "eq" && b.t === "eq") {
    return isEqualTerm(a.left, b.left) && isEqualTerm(a.right, b.right);
  }
  if (a.t === "litInt" && b.t === "litInt") {
    return a.value === b.value;
  }
  if (a.t === "rewrite" && b.t === "rewrite") {
    return (
      isEqualTerm(a.target, b.target) &&
      isEqualTerm(a.pattern, b.pattern) &&
      isEqualTerm(a.replacement, b.replacement)
    );
  }
  return false;
}

export function isEqualSubst(
  a: Subst | SubstPattern,
  b: Subst | SubstPattern,
): boolean {
  if (a.length !== b.length) return false;
  for (let i = 0; i < a.length; i++) {
    const ai = a[i];
    const bi = b[i];
    if (typeof ai === "number" && typeof bi === "number") {
      if (ai !== bi) return false;
    } else if (typeof ai === "object" && typeof bi === "object") {
      return isEqualTerm(ai.term, bi.term);
    } else {
      return false;
    }
  }
  return true;
}

export function isEqualContextItem(a: ContextItem, b: ContextItem): boolean {
  return (
    // a.name === b.name &&  // We do not compare names for equality
    a.level === b.level &&
    isEqualType(a.type, b.type) &&
    isEqualContext(a.deps, b.deps)
  );
}

export function isEqualContext(a: Context, b: Context): boolean {
  if (a.length !== b.length) return false;
  for (let i = 0; i < a.length; i++) {
    if (!isEqualContextItem(a[i], b[i])) return false;
  }
  return true;
}

export function isComparableType(type: Type): boolean {
  return (
    type.t === "bool" ||
    type.t === "int" ||
    (type.t === "code") ||
    (type.t === "wrap" && isComparableType(type.type))
  );
}

// Printing
function parenIf(cond: boolean, str: string): string {
  return cond ? `(${str})` : str;
}

export function printType(type: Type): string {
  switch (type.t) {
    case "bool":
      return "bool";
    case "int":
      return "int";
    case "code":
      return `${parenIf(type.type.t === "fun", printType(type.type))} code`;
    case "fun":
      return `${parenIf(type.arg.t === "fun", printType(type.arg))}${printDeps(type.deps)} -> ${printType(type.ret)}`;
    case "wrap":
      return `${parenIf(type.type.t === "fun", printType(type.type))} <| [${printContext(type.deps)}]`;
  }
}

function printDeps(deps: Context): string {
  return deps.length > 0 ? ` [${printContext(deps)}]` : "";
}

export function printContext(context: Context): string {
  return context.map(printContextItem).join("; ");
}

export function printContextItem(item: ContextItem): string {
  return `${item.name}@${item.level}: ${printType(item.type)}${printDeps(item.deps)}`;
}

export function printTerm(term: Pattern): string {
  switch (term.t) {
    case "patternVariable":
      return term.bind.name;
    case "wildcard":
      return "_"; // Wildcards are also printed as underscores
    case "varSubst":
      return `#${term.ix}${printSubst(term.subst)}`;
    case "true":
      return "true";
    case "false":
      return "false";
    case "if":
      return `if ${printTerm(term.cond)} then ${printTerm(term.then)} else ${printTerm(term.else)}`;
    case "ctxAbs":
      return `fun ${term.bind.name} : ${printType(term.bind.type)}${printDeps(term.bind.deps)} -> ${printTerm(term.body)}`;
    case "ctxApp":
      // TODO: Can use less parentheses in the argument position
      return `${parenIf(!isAtomicTerm(term.fun) && term.fun.t !== "ctxApp", printTerm(term.fun))} ${parenIf(!isAtomicTerm(term.arg), printTerm(term.arg))}`;
    case "quote":
      return `<${printTerm(term.term)}>`;
    case "letQuote":
      return `let$ ${term.bind.name} : ${printType(term.bind.type)}${printDeps(term.bind.deps)} = ${printTerm(term.def)} in ${printTerm(term.body)}`;
    case "wrap":
      return `wrap${printDeps(term.deps)} ${printTerm(term.term)}`;
    case "letWrap":
      return `let wrap ${term.bind.name} : ${printType(term.bind.type)} = ${printTerm(term.def)} in ${printTerm(term.body)}`;
    case "match":
      return `(match$ ${printTerm(term.target)} with ${term.branches.map(printMatchBranch).join("; ")})`;
    case "letRec":
      return `let rec ${term.bind.name} : ${printType(term.bind.type)}${printDeps(term.bind.deps)} = ${printTerm(term.def)} in ${printTerm(term.body)}`;
    case "arith":
      return `${parenIf(!isAtomicTerm(term.left) && term.left.t !== "ctxApp" && !(term.left.t === "arith" && precedence(term.left.op) >= precedence(term.op)), printTerm(term.left))} ${term.op} ${parenIf(!isAtomicTerm(term.right) && term.right.t !== "ctxApp" && !(term.left.t === "arith" && precedence(term.left.op) > precedence(term.op)), printTerm(term.right))}`;
    case "eq":
      return `${parenIf(!isAtomicTerm(term.left) && term.left.t !== "ctxApp" && term.left.t !== "arith" && term.left.t !== "eq", printTerm(term.left))} == ${parenIf(!isAtomicTerm(term.right) && term.right.t !== "ctxApp" && term.right.t !== "arith", printTerm(term.right))}`;
    case "litInt":
      return `${term.value}`;
    case "rewrite":
      return `${parenIf(!isAtomicTerm(term.target) && term.target.t !== "ctxApp" && term.target.t !== "arith" && term.target.t !== "eq" && term.target.t !== "rewrite", printTerm(term.target))} rewrite ${printTerm(term.pattern)} -> ${parenIf(!isAtomicTerm(term.replacement) && term.replacement.t !== "ctxApp" && term.replacement.t !== "arith" && term.replacement.t !== "eq", printTerm(term.replacement))}`;
  }
}

function precedence(op: "+" | "-" | "*" | "/"): number {
  switch (op) {
    case "+":
    case "-":
      return 1;
    case "*":
    case "/":
      return 2;
  }
}

function printMatchBranch(branch: MatchBranchPattern): string {
  return `| ${printTerm(branch.pattern)} -> ${printTerm(branch.body)}`;
}

export function printSubst(subst: SubstPattern): string {
  return subst.length > 0
    ? `[${subst.map((s, i) => `#${i} = ${typeof s === "number" ? `#${s}` : printTerm(s.term)}`).join("; ")}]`
    : "";
}

function isAtomicTerm(term: Pattern): boolean {
  return (
    term.t === "litInt" ||
    term.t === "true" ||
    term.t === "false" ||
    term.t === "varSubst" ||
    term.t === "quote" ||
    term.t === "wildcard" ||
    term.t === "patternVariable"
  );
}

export function match(pattern: Pattern, term: Term): Subst | null {
  if (pattern.t === "patternVariable") {
    return [{ term, level: pattern.bind.level, deps: pattern.bind.deps }]; // TODO: the deps may have been renamed..
  }
  if (pattern.t === "wildcard") {
    return []; // Wildcards match anything, no substitution needed
  }

  switch (pattern.t) {
    case "varSubst":
      if (pattern.t !== term.t) return null;
      if (pattern.ix != term.ix) {
        return null; // Different indices cannot match
      }
      return matchSubst(pattern.subst, term.subst);
    case "true":
    case "false":
      if (pattern.t !== term.t) return null;
      return [];
    case "if": {
      if (pattern.t !== term.t) return null;
      const subst1 = match(pattern.cond, term.cond);
      if (subst1 == null) return null;
      const subst2 = match(pattern.then, term.then);
      if (subst2 == null) return null;
      const subst3 = match(pattern.else, term.else);
      if (subst3 == null) return null;
      return [...subst1, ...subst2, ...subst3];
    }
    case "ctxAbs":
      if (pattern.t !== term.t) return null;
      return match(pattern.body, term.body);
    case "ctxApp": {
      if (pattern.t !== term.t) return null;
      const subst1 = match(pattern.fun, term.fun);
      if (subst1 == null) return null;
      const subst2 = match(pattern.arg, term.arg);
      if (subst2 == null) return null;
      return [...subst1, ...subst2];
    }
    case "quote":
      if (pattern.t !== term.t) return null;
      return match(pattern.term, term.term);
    case "letQuote": {
      if (pattern.t !== term.t) return null;
      const subst1 = match(pattern.def, term.def);
      if (subst1 == null) return null;
      const subst2 = match(pattern.body, term.body);
      if (subst2 == null) return null;
      return [...subst1, ...subst2];
    }
    case "wrap":
      if (pattern.t !== term.t) return null;
      return match(pattern.term, term.term);
    case "letWrap": {
      if (pattern.t !== term.t) return null;
      const subst1 = match(pattern.def, term.def);
      if (subst1 == null) return null;
      const subst2 = match(pattern.body, term.body);
      if (subst2 == null) return null;
      return [...subst1, ...subst2];
    }
    case "match": {
      if (pattern.t !== term.t) return null;
      const result = match(pattern.target, term.target);
      if (result == null) return null;
      if (pattern.branches.length !== term.branches.length) {
        return null; // Different number of branches cannot match
      }
      for (let i = 0; i < pattern.branches.length; i++) {
        const p = pattern.branches[i];
        const t = term.branches[i];
        if (!isEqualTerm(p.pattern, t.pattern)) return null;
        const subst = match(p.body, t.body);
        if (subst == null) return null;
        result.push(...subst);
      }
      return result;
    }
    case "letRec": {
      if (pattern.t !== term.t) return null;
      const subst1 = match(pattern.def, term.def);
      if (subst1 == null) return null;
      const subst2 = match(pattern.body, term.body);
      if (subst2 == null) return null;
      return [...subst1, ...subst2];
    }
    case "arith": {
      if (pattern.t !== term.t) return null;
      if (pattern.op !== term.op) return null;
      const subst1 = match(pattern.left, term.left);
      if (subst1 == null) return null;
      const subst2 = match(pattern.right, term.right);
      if (subst2 == null) return null;
      return [...subst1, ...subst2];
    }
    case "eq": {
      if (pattern.t !== term.t) return null;
      const subst1 = match(pattern.left, term.left);
      if (subst1 == null) return null;
      const subst2 = match(pattern.right, term.right);
      if (subst2 == null) return null;
      return [...subst1, ...subst2];
    }
    case "litInt": {
      if (pattern.t !== term.t) return null;
      if (pattern.value !== term.value) return null;
      return [];
    }
    case "rewrite": {
      if (pattern.t !== term.t) return null;
      const subst1 = match(pattern.target, term.target);
      if (subst1 == null) return null;
      if (!isEqualTerm(pattern.pattern, term.pattern)) return null;
      const subst2 = match(pattern.replacement, term.replacement);
      if (subst2 == null) return null;
      return [...subst1, ...subst2];
    }
    default: {
      let _: never = pattern; // Exhaustive check
    }
  }
  return null;
}

function matchSubst(pattern: SubstPattern, target: Subst): Subst | null {
  if (pattern.length !== target.length) {
    return null; // Different lengths cannot match
  }
  const result: Subst = [];
  for (let i = 0; i < pattern.length; i++) {
    const p = pattern[i];
    const t = target[i];
    if (typeof p === "number" && typeof t === "number") {
      if (p !== t) return null; // Different indices cannot match
      result.push(p); // Keep the index
    } else if (typeof p === "object" && typeof t === "object") {
      const m = match(p.term, t.term);
      if (m == null) return null; // No match
      result.push(...m);
    } else {
      return null; // Mixed types cannot match
    }
  }
  return result;
}
