import {
  rename,
  substitute,
  substituteSubst,
  substituteSubstItem,
  ids,
  exts,
  renameSubst,
  applyBottomUp,
} from "./substitution";
import { Term, Subst, SubstItem, Context, Ix, match, printTerm, isEqualTerm } from "./ast";

type Denotation =
  | { t: "value"; term: Term } // Value type
  | { t: "wrap"; term: Term; denotation: Denotation; deps: Context } // Wrap type
  | {
      t: "fun";
      term: Term;
      func: (
        subst: Subst,
        arg: Denotation,
        newContextLength: number,
      ) => Denotation;
    }; // Function type

// wrap type is just changing the dependencies

type CurrentStageItem = {
  term: Term;
  deps: Context;
  func: (subst: Subst, arg: Env, newContextLength: number) => Denotation;
};
type EnvItem = SubstItem | CurrentStageItem;

type Env = EnvItem[];

function substituteDenotation(
  d: Denotation,
  subst: Subst,
  newContextLength: number,
): Denotation {
  if (d.t === "value") {
    return { t: "value", term: substitute(d.term, subst, newContextLength) };
  } else if (d.t === "fun") {
    return {
      t: "fun",
      term: substitute(d.term, subst, newContextLength),
      func: (s: Subst, arg: Denotation, newContextLengthOfS: number) => {
        if (newContextLength !== s.length) {
          throw new Error(
            `Context length mismatch: expected ${newContextLength}, got ${s.length}`,
          );
        }
        return d.func(
          substituteSubst(subst, s, newContextLengthOfS),
          arg,
          newContextLengthOfS,
        );
      },
    };
  } else if (d.t === "wrap") {
    return {
      t: "wrap",
      term: substitute(d.term, subst, newContextLength),
      denotation: substituteDenotation(
        d.denotation,
        exts(subst, d.deps.length),
        newContextLength + d.deps.length,
      ),
      deps: d.deps,
    };
  }
  throw new Error("Unknown denotation type");
}

function substituteEnv(env: Env, subst: Subst, newContextLength: number): Env {
  return env.map((item) => {
    if (typeof item === "object" && "func" in item) {
      // Current stage entry
      return {
        term: substitute(
          item.term,
          exts(subst, item.deps.length),
          newContextLength + item.deps.length,
        ),
        deps: item.deps,
        func: (s: Subst, arg: Env, newContextLengthOfS: number) => {
          if (newContextLength !== s.length) {
            throw new Error(
              `Context length mismatch: expected ${newContextLength}, got ${s.length}`,
            );
          }
          return item.func(
            substituteSubst(subst, s, newContextLengthOfS),
            arg,
            newContextLengthOfS,
          );
        },
      };
    } else {
      // Later stage entry
      return substituteSubstItem(item, subst, newContextLength);
    }
  });
}

function extEnv(env: Env, offset: number, newContextLength: number): Env {
  return (ids(offset) as Env).concat(
    substituteEnv(
      env,
      renameSubst(ids(newContextLength), (ix) => ix + offset),
      newContextLength + offset,
    ),
  );
}

function single(d: Denotation, deps: Context): CurrentStageItem {
  return {
    term: d.term,
    deps,
    func: (s: Subst, env: Env, newContextLength: number) =>
      substituteDenotation(d, (env as Subst).concat(s), newContextLength),
  };
}

export function interpret(
  currentStage: number,
  term: Term,
  env: Env,
  newContextLength: number,
): Denotation {
  switch (term.t) {
    case "varSubst": {
      const entry = env[term.ix];
      if (!(typeof entry === "object" && "func" in entry)) {
        throw new Error(
          `Expected current stage entry at index ${term.ix}, but found later stage.`,
        );
      }
      return entry.func(
        ids(newContextLength),
        interpretSubst(currentStage, term.subst, env, newContextLength),
        newContextLength,
      );
    }
    case "true":
      return { t: "value", term };
    case "false":
      return { t: "value", term };
    case "if": {
      const cond = interpret(currentStage, term.cond, env, newContextLength);
      return cond.term.t === "true"
        ? interpret(currentStage, term.then, env, newContextLength)
        : interpret(currentStage, term.else, env, newContextLength);
    }
    case "ctxAbs":
      return {
        t: "fun",
        term,
        func(s: Subst, d: Denotation, newContextLengthOfS: number): Denotation {
          if (newContextLength != s.length) {
            throw new Error(
              `Context length mismatch: expected ${newContextLength}, got ${s.length}`,
            );
          }
          return interpret(
            currentStage,
            term.body,
            [
              single(d, term.bind.deps),
              ...substituteEnv(env, s, newContextLengthOfS),
            ],
            newContextLengthOfS,
          );
        },
      };
    case "ctxApp": {
      const dFun = interpret(currentStage, term.fun, env, newContextLength);
      if (!("func" in dFun)) {
        throw new Error(
          "Expected a function denotation for the function term, but found a value.",
        );
      }
      const dArg = interpret(
        currentStage,
        term.arg,
        extEnv(env, term.deps.length, newContextLength),
        newContextLength + term.deps.length,
      );
      return dFun.func(ids(newContextLength), dArg, newContextLength);
    }
    case "quote":
      return {
        t: "value",
        term: substitute(term, env as Subst, newContextLength),
      };
    case "letQuote": {
      const defTerm = interpret(
        currentStage,
        term.def,
        extEnv(env, term.bind.deps.length, newContextLength),
        newContextLength + term.bind.deps.length,
      ).term;
      if (defTerm.t !== "quote") {
        throw new Error(
          "Expected a quoted term in letQuote definition, but found: " +
            defTerm.t,
        );
      }
      return interpret(
        currentStage,
        term.body,
        [
          { term: defTerm.term, deps: term.bind.deps, level: term.bind.level },
          ...env,
        ],
        newContextLength,
      );
    }
    case "wrap":
      let body = interpret(
        currentStage,
        term.term,
        extEnv(env, term.deps.length, newContextLength),
        newContextLength + term.deps.length,
      );
      return {
        t: "wrap",
        term: { t: "wrap", term: body.term, deps: term.deps },
        denotation: body,
        deps: term.deps,
      };
    case "letWrap": {
      const def = interpret(currentStage, term.def, env, newContextLength);
      if (def.t !== "wrap") {
        throw new Error(
          "Expected a wrap term in letWrap definition, but found: " + def.t,
        );
      }
      return interpret(
        currentStage,
        term.body,
        [single(def.denotation, term.bind.deps), ...env],
        newContextLength,
      );
    }
    case "match": {
      const target = interpret(
        currentStage,
        term.target,
        env,
        newContextLength,
      );
      if (target.term.t !== "quote") {
        throw new Error(
          "Expected a quoted term in match target, but found: " + target.term.t,
        );
      }
      for (const branch of term.branches) {
        const newPattern = substitute(
          branch.pattern,
          env as Subst,
          newContextLength,
        );
        const subst = match(newPattern, target.term.term);
        if (subst == null) continue;
        return interpret(
          currentStage,
          branch.body,
          (subst as Env).concat(env),
          newContextLength,
        );
      }
      throw new Error("No matching branch found in match expression.");
    }
    case "letRec": {
      const currentContextLength = env.length;
      const newDef = substitute(
        term.def,
        [
          {
            term: {
              ...term,
              body: {
                t: "varSubst",
                ix: 0,
                subst: renameSubst(ids(term.bind.deps.length), (x) => x + 1),
              },
            },
            deps: term.bind.deps,
            level: currentStage,
          },
          ...ids(currentContextLength + term.bind.deps.length),
        ],
        currentContextLength + term.bind.deps.length,
      );
      const def = interpret(
        currentStage,
        newDef,
        extEnv(env, term.bind.deps.length, newContextLength),
        newContextLength + term.bind.deps.length,
      );
      return interpret(
        currentStage,
        term.body,
        [single(def, term.bind.deps), ...env],
        newContextLength,
      );
    }
    case "arith": {
      const left = interpret(currentStage, term.left, env, newContextLength);
      const right = interpret(currentStage, term.right, env, newContextLength);
      if (left.term.t !== "litInt" || right.term.t !== "litInt") {
        throw new Error(`Both sides of ${term.op} must be integers.`);
      }
      const value = interpretArith(term.op, left.term.value, right.term.value);
      return {
        t: "value",
        term: { t: "litInt", value },
      };
    }
    case "eq": {
      const left = interpret(currentStage, term.left, env, newContextLength);
      const right = interpret(currentStage, term.right, env, newContextLength);
      return { t: "value", term: { t: isEqualTerm(left.term, right.term) ? "true" : "false" } };
    }
    case "litInt":
      return { t: "value", term };
    case "rewrite": {
      const target = interpret(
        currentStage,
        term.target,
        env,
        newContextLength,
      );
      if (target.term.t !== "quote") {
        throw new Error(
          "Expected a quoted term in rewrite target, but found: " +
            target.term.t,
        );
      }
      const newPattern = substitute(
        term.pattern,
        env as Subst,
        newContextLength,
      );
      return {
        t: "value",
        term: {
          t: "quote",
          term: applyBottomUp(
            currentStage + 1,
            newContextLength,
            (newContextLengthOfSubterm, subterm) => {
              let m = match(
                rename(
                  newPattern,
                  (ix) => ix + (newContextLengthOfSubterm - newContextLength),
                ),
                subterm,
              );
              if (m == null) {
                return subterm; // No match, return original term
              }
              // Eval the replacement term with the matched substitution
              let replacement = interpret(
                currentStage,
                term.replacement,
                (m as Env).concat(
                  substituteEnv(
                    env,
                    renameSubst(
                      ids(newContextLength),
                      (ix) =>
                        ix + (newContextLengthOfSubterm - newContextLength),
                    ),
                    newContextLengthOfSubterm,
                  ),
                ),
                newContextLengthOfSubterm,
              );
              if (replacement.term.t !== "quote") {
                throw new Error(
                  "Replacement term must evaluate to a quoted term",
                );
              }
              return replacement.term.term;
            },
            target.term.term,
          ),
        },
      };
    }
    default:
      return term satisfies never;
  }
}

function interpretSubst(
  currentStage: number,
  subst: Subst,
  env: Env,
  newContextLength: number,
): Env {
  return subst.map((item) => {
    if (typeof item === "number") {
      return env[item];
    }
    if (item.level == currentStage) {
      return {
        term: item.term,
        deps: item.deps,
        func: (s: Subst, arg: Env, newContextLengthOfS: number) =>
          interpret(
            currentStage,
            item.term,
            arg.concat(substituteEnv(env, s, newContextLengthOfS)),
            newContextLengthOfS,
          ),
      };
    } else {
      return substituteSubstItem(item, env as Subst, newContextLength);
    }
  });
}

function interpretArith(op: "+" | "-" | "*" | "/", left: number, right: number): number {
  switch (op) {
    case "+":
      return left + right;
    case "-":
      return left - right;
    case "*":
      return left * right;
    case "/":
      return left / right;
  }
}
