import type { SyntaxNode } from "@lezer/common";
import type {
  Type,
  Term,
  Context,
  ContextItem,
  Pattern,
  Subst,
  SubstItemPattern,
  Ix,
  MatchBranchPattern,
} from "./ast.ts";
import { isEqualContext, isEqualType, isComparableType } from "./ast.ts";

type Env = {
  source: string;
  context: Context;
  level: number;
  expectedType?: Type;
} & (
  | {
      isPattern: true;
      outerContextLength: number;
    }
  | {
      isPattern: false;
    }
);

// For regular terms, the context is given and the type is inferred
type TypedTerm = {
  term: Pattern;
  type: Type;
  patternVariables: Context;
};

export class TypeInferenceError extends Error {
  node: SyntaxNode;
  constructor(message: string, node: SyntaxNode) {
    super(message);
    this.name = "TypeInferenceError";
    this.node = node;
  }
}

export function inferTerm<E extends Env>(node: SyntaxNode, env: E): TypedTerm {
  let result: TypedTerm | null = null;
  switch (node.name) {
    case "PatternVariable":
      result = inferPatternVariable(node, env);
      break;
    case "WildcardPattern":
      result = inferWildcardPattern(node, env);
      break;
    case "VarSubstTerm":
    case "VarSubstPattern":
      result = inferVarSubstTerm(node, env);
      break;
    case "TrueTerm":
    case "TruePattern":
      result = inferTrueTerm(node, env);
      break;
    case "FalseTerm":
    case "FalsePattern":
      result = inferFalseTerm(node, env);
      break;
    case "IfTerm":
    case "IfPattern":
      result = inferIfTerm(node, env);
      break;
    case "CtxAbsTerm":
    case "CtxAbsPattern":
      result = inferCtxAbsTerm(node, env);
      break;
    case "CtxAppTerm":
    case "CtxAppPattern":
      result = inferCtxAppTerm(node, env);
      break;
    case "LetTerm":
    case "LetPattern":
      result = inferLetTerm(node, env);
      break;
    case "LetRecTerm":
    case "LetRecPattern":
      result = inferLetRecTerm(node, env);
      break;
    case "LetQuoteTerm":
    case "LetQuotePattern":
      result = inferLetQuoteTerm(node, env);
      break;
    case "QuoteTerm":
    case "QuotePattern":
      result = inferQuoteTerm(node, env);
      break;
    case "WrapTerm":
    case "WrapPattern":
      result = inferWrapTerm(node, env);
      break;
    case "LetWrapTerm":
    case "LetWrapPattern":
      result = inferLetWrapTerm(node, env);
      break;
    case "MatchTerm":
    case "MatchPattern":
      result = inferMatchTerm(node, env);
      break;
    case "AddTerm":
    case "AddPattern":
    case "MultTerm":
    case "MultPattern":
      result = inferArithTerm(node, env);
      break;
    case "EqTerm":
      result = inferEqTerm(node, env);
      break;
    case "LitIntTerm":
    case "LitIntPattern":
      result = inferLitIntTerm(node, env);
      break;
    case "RewriteTerm":
    case "RewritePattern":
      result = inferRewriteTerm(node, env);
      break;
    case "AnnTerm":
    case "AnnPattern":
      result = inferAnnTerm(node, env);
      break;
  }
  if (!result) {
    throw new Error(`Unknown term node: ${node.name}`);
  }
  if (env.expectedType && !isEqualType(result.type, env.expectedType)) {
    throw new TypeInferenceError(
      `Expected ${JSON.stringify(env.expectedType)}, but got ${JSON.stringify(result.type)}`,
      node,
    );
  }
  return result;
}

function inferPatternVariable<E extends Env>(
  node: SyntaxNode,
  env: E,
): TypedTerm {
  const name = readText(node.getChild("VariableName")!, env);
  if (!env.isPattern) {
    throw new TypeInferenceError(`Unexpected pattern variable '${name}'`, node);
  }
  const deps = env.context.slice(
    0,
    env.context.length - env.outerContextLength,
  );
  if (env.expectedType == null) {
    throw new TypeInferenceError(
      `Cannot infer type for pattern variable '${name}'`,
      node,
    );
  }
  return {
    term: {
      t: "patternVariable",
      bind: { name, level: env.level, type: env.expectedType, deps },
    },
    type: env.expectedType,
    patternVariables: [
      { name, level: env.level, type: env.expectedType, deps },
    ],
  };
}

function inferWildcardPattern(node: SyntaxNode, env: Env): TypedTerm {
  if (!env.isPattern) {
    throw new TypeInferenceError("Unexpected wildcard pattern", node);
  }
  if (env.expectedType == null) {
    throw new TypeInferenceError(
      "Cannot infer type for wildcard pattern",
      node,
    );
  }
  return {
    term: { t: "wildcard" },
    type: env.expectedType,
    patternVariables: [],
  };
}

function inferVarSubstTerm(node: SyntaxNode, env: Env): TypedTerm {
  const name = readText(node.getChild("VariableName")!, env);
  const ix = env.context.findIndex((item) => item.name === name);
  if (ix === -1) {
    throw new TypeInferenceError(
      `Variable '${name}' not found in context`,
      node.getChild("VariableName")!,
    );
  }
  if (env.context[ix].level != env.level) {
    throw new TypeInferenceError(
      `Variable '${name}' at level ${env.context[ix].level} cannot be used at level ${env.level}`,
      node.getChild("VariableName")!,
    );
  }
  const substNode = node.getChild("Subst");
  const domain = env.context[ix].deps;
  const { partialSubst, patternVariables } = substNode
    ? readSubst(substNode, domain, env)
    : { partialSubst: [], patternVariables: [] };
  if (env.isPattern && ix > env.context.length - env.outerContextLength) {
    // If the variable is in the outer context, the substitution must not contain any pattern variables
    if (patternVariables.length > 0) {
      throw new TypeInferenceError(
        `Pattern variables are not allowed in substitutions for outer context variables`,
        substNode!,
      );
    }
  }
  const subst = env.context[ix].deps.map((dep, i) => {
    if (partialSubst[i] != null) {
      return partialSubst[i];
    }
    // If the dependency is not in the substitution, we need to find it in the context
    const depIx = env.context.findIndex((item) => item.name === dep.name);
    if (depIx === -1) {
      throw new TypeInferenceError(
        `Dependency '${dep.name}' of '${name}' not found in context`,
        node.getChild("VariableName")!,
      );
    }
    if (env.context[depIx].level !== dep.level) {
      throw new TypeInferenceError(
        `Dependency '${dep.name}' of '${name}' is at level ${env.context[depIx].level}, but expected at level ${dep.level}`,
        node.getChild("VariableName")!,
      );
    }
    return depIx;
  });
  return {
    term: { t: "varSubst", ix, subst },
    type: env.context[ix].type,
    patternVariables,
  };
}

function inferTrueTerm(node: SyntaxNode, env: Env): TypedTerm {
  return {
    type: { t: "bool" },
    term: { t: "true" },
    patternVariables: [],
  };
}

function inferFalseTerm(node: SyntaxNode, env: Env): TypedTerm {
  return {
    type: { t: "bool" },
    term: { t: "false" },
    patternVariables: [],
  };
}

function inferIfTerm(node: SyntaxNode, env: Env): TypedTerm {
  const [condNode, thenNode, elseNode] = node.getChildren("Term");
  const cond = inferTerm(condNode, { ...env, expectedType: { t: "bool" } });
  const thenTyped = inferTerm(thenNode, env);
  const elseTyped = inferTerm(elseNode, {
    ...env,
    expectedType: thenTyped.type,
  });
  return {
    type: thenTyped.type,
    term: {
      t: "if",
      cond: cond.term,
      then: thenTyped.term,
      else: elseTyped.term,
    },
    patternVariables: [
      ...cond.patternVariables,
      ...thenTyped.patternVariables,
      ...elseTyped.patternVariables,
    ],
  };
}

function inferCtxAbsTerm(node: SyntaxNode, env: Env): TypedTerm {
  if (env.expectedType && env.expectedType.t !== "fun") {
    throw new TypeInferenceError(
      `Expected ${JSON.stringify(env.expectedType)}, but got a function type`,
      node,
    );
  }

  const name = readText(node.getChild("VariableName")!, env);
  const type =
    readOptionalType(node.getChild("Type"), env) ?? env.expectedType?.arg;

  if (type == null) {
    throw new TypeInferenceError(
      `Cannot infer type for function argument '${name}'`,
      node.getChild("VariableName")!,
    );
  }

  const deps =
    readOptionalContext(node.getChild("Context"), {
      ...env,
      level: env.level + 1,
    }) ??
    env.expectedType?.deps ??
    [];

  const bind = { name, level: env.level, type, deps };
  const body = inferTerm(node.getChild("Term")!, {
    ...env,
    context: [bind, ...env.context],
    expectedType: env.expectedType && env.expectedType.ret,
  });
  return {
    type: {
      t: "fun",
      arg: type,
      deps: deps,
      ret: body.type,
    },
    term: { t: "ctxAbs", bind, body: body.term },
    patternVariables: body.patternVariables,
  };
}

function inferCtxAppTerm(node: SyntaxNode, env: Env): TypedTerm {
  const [funNode, argNode] = node.getChildren("Term");
  const fun = inferTerm(funNode, { ...env, expectedType: undefined });
  if (fun.type.t !== "fun") {
    throw new TypeInferenceError("Cannot apply a non-function term", funNode);
  }
  const arg = inferTerm(argNode, {
    ...env,
    context: fun.type.deps.concat(env.context),
    expectedType: fun.type.arg,
  });
  return {
    type: fun.type.ret,
    term: { t: "ctxApp", fun: fun.term, deps: fun.type.deps, arg: arg.term },
    patternVariables: [...fun.patternVariables, ...arg.patternVariables],
  };
}

function inferLetTerm(node: SyntaxNode, env: Env): TypedTerm {
  const name = readText(node.getChild("VariableName")!, env);
  const type = readOptionalType(node.getChild("Type")!, env);
  const deps =
    readOptionalContext(node.getChild("Context"), {
      ...env,
      level: env.level + 1,
    }) ?? [];
  const [defNode, bodyNode] = node.getChildren("Term");
  const def = inferTerm(defNode, {
    ...env,
    context: deps.concat(env.context),
    expectedType: type || undefined,
  });
  const bind = { name, level: env.level, type: def.type, deps };
  const body = inferTerm(bodyNode, {
    ...env,
    context: [bind, ...env.context],
  });
  return {
    type: body.type,
    term: {
      t: "ctxApp",
      fun: { t: "ctxAbs", bind, body: body.term },
      deps: deps,
      arg: def.term,
    },
    patternVariables: [...def.patternVariables, ...body.patternVariables],
  };
}

function inferLetRecTerm(node: SyntaxNode, env: Env): TypedTerm {
  const name = readText(node.getChild("VariableName")!, env);
  const type = readType(node.getChild("Type")!, env);
  const deps =
    readOptionalContext(node.getChild("Context"), {
      ...env,
      level: env.level + 1,
    }) ?? [];
  const [defNode, bodyNode] = node.getChildren("Term");
  const bind = { name, level: env.level, type, deps };
  const def = inferTerm(defNode, {
    ...env,
    context: [bind, ...deps, ...env.context],
    expectedType: type,
  });
  if (!isEqualType(def.type, type)) {
    throw new TypeInferenceError(
      `Expected the definition to be of type ${JSON.stringify(type)}, but got ${JSON.stringify(def.type)}`,
      defNode,
    );
  }

  const body = inferTerm(bodyNode, {
    ...env,
    context: [bind, ...env.context],
  });
  return {
    type: body.type,
    term: {
      t: "letRec",
      bind,
      def: def.term,
      body: body.term,
    },
    patternVariables: [...def.patternVariables, ...body.patternVariables],
  };
}

function inferLetQuoteTerm(node: SyntaxNode, env: Env): TypedTerm {
  const name = readText(node.getChild("VariableName")!, env);
  const type = readOptionalType(node.getChild("Type")!, {
    ...env,
    level: env.level + 1,
  });
  const deps =
    readOptionalContext(node.getChild("Context"), {
      ...env,
      level: env.level + 1,
    }) ?? [];
  const [defNode, bodyNode] = node.getChildren("Term");
  const def = inferTerm(defNode, {
    ...env,
    context: deps.concat(env.context),
    expectedType: type != null ? { t: "code", type } : undefined,
  });
  if (def.type.t !== "code") {
    throw new TypeInferenceError(
      `Expected the definition to be of type 'code', but got ${JSON.stringify(def.type)}`,
      defNode,
    );
  }
  const bind = { name, level: env.level + 1, type: def.type.type, deps };
  const body = inferTerm(bodyNode, {
    ...env,
    context: [bind, ...env.context],
  });
  return {
    type: body.type,
    term: { t: "letQuote", bind, def: def.term, body: body.term },
    patternVariables: [...def.patternVariables, ...body.patternVariables],
  };
}

function inferQuoteTerm(node: SyntaxNode, env: Env): TypedTerm {
  if (env.expectedType && env.expectedType.t !== "code") {
    throw new TypeInferenceError(
      `Expected ${JSON.stringify(env.expectedType)}, but got a code type`,
      node,
    );
  }
  const termNode = node.getChild("Term")!;
  const { type, term } = inferTerm(termNode, {
    ...env,
    // We do not filter the context unlike in rule `Quote`.
    // Since the level of the variable is checked in the `VarSubstTerm` rule,
    // this should be fine.
    // context: env.context.filter((item) => item.level >= env.level + 1),
    level: env.level + 1,
    expectedType: env.expectedType?.type,
  });
  return {
    type: { t: "code", type },
    term: { t: "quote", term },
    patternVariables: [],
  };
}

function inferWrapTerm(node: SyntaxNode, env: Env): TypedTerm {
  if (env.expectedType && env.expectedType.t !== "wrap") {
    throw new TypeInferenceError(
      `Expected ${JSON.stringify(env.expectedType)}, but got a wrap type`,
      node,
    );
  }
  const deps =
    readOptionalContext(node.getChild("Context"), {
      ...env,
      level: env.level + 1,
    }) ?? [];
  const body = inferTerm(node.getChild("Term")!, {
    ...env,
    context: deps.concat(env.context),
    expectedType: env.expectedType?.type,
  });
  return {
    type: { t: "wrap", deps, type: body.type },
    term: { t: "wrap", deps, term: body.term },
    patternVariables: body.patternVariables,
  };
}

function inferLetWrapTerm(node: SyntaxNode, env: Env): TypedTerm {
  const name = readText(node.getChild("VariableName")!, env);
  const type = readOptionalType(node.getChild("Type")!, env);
  const deps = readOptionalContext(node.getChild("Context"), {
    ...env,
    level: env.level + 1,
  });
  const [defNode, bodyNode] = node.getChildren("Term");
  const def = inferTerm(defNode, {
    ...env,
    expectedType: (type && deps && { t: "wrap", type, deps }) || undefined,
  });
  if (def.type.t !== "wrap") {
    throw new TypeInferenceError(
      `Expected the definition to be of type 'wrap', but got ${JSON.stringify(def.type)}`,
      defNode,
    );
  }
  if (type && !isEqualType(def.type.type, type)) {
    throw new TypeInferenceError(
      `Definition type mismatch: expected ${JSON.stringify(type)}, but got ${JSON.stringify(def.type)}`,
      defNode,
    );
  }
  if (deps && !isEqualContext(def.type.deps, deps)) {
    throw new TypeInferenceError(
      `Definition dependencies mismatch: expected ${JSON.stringify(deps)}, but got ${JSON.stringify(def.type.deps)}`,
      defNode,
    );
  }
  const bind = {
    name,
    level: env.level,
    type: def.type.type,
    deps: def.type.deps,
  };
  const body = inferTerm(bodyNode, {
    ...env,
    context: [bind, ...env.context],
  });
  return {
    type: body.type,
    term: { t: "letWrap", bind, def: def.term, body: body.term },
    patternVariables: [...def.patternVariables, ...body.patternVariables],
  };
}

function inferMatchTerm(node: SyntaxNode, env: Env): TypedTerm {
  const target = inferTerm(node.getChild("Term")!, env);
  if (target.type.t !== "code") {
    throw new TypeInferenceError(
      `Match target must be of type 'code', but got ${JSON.stringify(target.type)}`,
      node.getChild("Term")!,
    );
  }
  const targetType = target.type.type;
  let bodyType: Type | null = null;
  const branchPatternVariables: Context[] = [];
  const branches = node
    .getChildren(env.isPattern ? "MatchBranchPattern" : "MatchBranch")
    .map<MatchBranchPattern>((branchNode) => {
      const [patternNode, bodyNode] = branchNode.getChildren("Term");
      const { term: pattern, patternVariables } = inferTerm(patternNode, {
        ...env,
        level: env.level + 1,
        isPattern: true,
        outerContextLength: env.context.length,
        expectedType: targetType,
      });
      const body = inferTerm(bodyNode, {
        ...env,
        outerContextLength: env.context.length,
        context: patternVariables.concat(env.context),
      });
      if (bodyType === null) {
        bodyType = body.type;
      } else if (!isEqualType(body.type, bodyType)) {
        throw new TypeInferenceError(
          `Branch type mismatch: expected ${JSON.stringify(bodyType)}, but got ${JSON.stringify(body.type)}`,
          branchNode.getChild("Term")!,
        );
      }
      branchPatternVariables.push(body.patternVariables);
      return { pattern, patternVariables, body: body.term };
    });
  if (bodyType === null) {
    throw new TypeInferenceError("No branches in match expression", node);
  }
  return {
    term: { t: "match", target: target.term, branches },
    type: bodyType,
    patternVariables: target.patternVariables.concat(...branchPatternVariables),
  };
}

function inferArithTerm(node: SyntaxNode, env: Env): TypedTerm {
  const [leftNode, rightNode] = node.getChildren("Term");
  const left = inferTerm(leftNode, { ...env, expectedType: { t: "int" } });
  const right = inferTerm(rightNode, { ...env, expectedType: { t: "int" } });
  const op = readText(node.getChild("ArithOp")!, env);
  if (op !== "+" && op !== "-" && op !== "*" && op !== "/") {
    throw new TypeInferenceError(
      `Unknown arithmetic operator '${op}'`,
      node.getChild("ArithOp")!,
    );
  }
  return {
    term: { t: "arith", op, left: left.term, right: right.term },
    type: { t: "int" },
    patternVariables: [...left.patternVariables, ...right.patternVariables],
  };
}

function inferEqTerm(node: SyntaxNode, env: Env): TypedTerm {
  const [leftNode, rightNode] = node.getChildren("Term");
  const left = inferTerm(leftNode, { ...env, expectedType: undefined });
  if (!isComparableType(left.type)) {
    throw new TypeInferenceError(
      `Cannot compare values of type ${JSON.stringify(left.type)}`,
      leftNode,
    );
  }
  const right = inferTerm(rightNode, { ...env, expectedType: left.type });
  if (!isEqualType(left.type, right.type)) {
    throw new TypeInferenceError(
      `Left and right sides of == must have the same type, but got ${JSON.stringify(left.type)} and ${JSON.stringify(right.type)}`,
      rightNode,
    );
  }
  return {
    term: { t: "eq", left: left.term, right: right.term },
    type: { t: "bool" },
    patternVariables: [...left.patternVariables, ...right.patternVariables],
  };
}

function inferLitIntTerm(node: SyntaxNode, env: Env): TypedTerm {
  const value = parseInt(env.source.slice(node.from, node.to), 10);
  return {
    term: { t: "litInt", value },
    type: { t: "int" },
    patternVariables: [],
  };
}

function inferRewriteTerm(node: SyntaxNode, env: Env): TypedTerm {
  const [targetNode, patternNode, replacementNode] = node.getChildren("Term");
  const target = inferTerm(targetNode, env);
  if (target.type.t !== "code") {
    throw new TypeInferenceError(
      `Rewrite target must be of type 'code', but got ${JSON.stringify(target.type)}`,
      targetNode,
    );
  }
  // TODO: type of a pattern cannot be inferred, so we need some kind of annotation here
  const pattern = inferTerm(patternNode, {
    ...env,
    level: env.level + 1,
    expectedType: undefined,
    isPattern: true,
    outerContextLength: env.context.length,
  });
  const replacement = inferTerm(replacementNode, {
    ...env,
    context: pattern.patternVariables.concat(env.context),
    expectedType: { t: "code", type: pattern.type },
  });
  return {
    term: {
      t: "rewrite",
      target: target.term,
      pattern: pattern.term,
      replacement: replacement.term,
      patternVariables: pattern.patternVariables,
    },
    type: target.type,
    patternVariables: [
      ...target.patternVariables,
      ...replacement.patternVariables,
    ],
  };
}

function inferAnnTerm(node: SyntaxNode, env: Env): TypedTerm {
  const termNode = node.getChild("Term")!;
  const expectedType = readType(node.getChild("Type")!, env);
  return inferTerm(termNode, { ...env, expectedType });
}


function readOptionalContext(
  node: SyntaxNode | null,
  env: Env,
): Context | null {
  if (node == null) return null;
  return node
    .getChildren("ContextItem")
    .map((itemNode) => readContextItem(itemNode, env));
}

function readContextItem(
  node: SyntaxNode,
  env: Env,
  shiftDeps = false,
): ContextItem {
  const name = readText(node.getChild("VariableName")!, env);
  const levelNode = node.getChild("Level");
  const level = levelNode ? readLevel(levelNode, env) : env.level; // Default to current level if not specified
  if (level < env.level) {
    throw new TypeInferenceError(
      `Dependency '${name}' at level ${level} cannot be declared at level ${env.level}`,
      levelNode!,
    );
  }
  const type = readType(node.getChild("Type")!, env);
  const contextNode = node.getChild("Context");
  const deps =
    readOptionalContext(contextNode, {
      ...env,
      level: shiftDeps ? level + 1 : level,
    }) ?? [];
  return { name, level, type, deps };
}

function readText(node: SyntaxNode, env: Env): string {
  return env.source.slice(node.from, node.to);
}

function readLevel(node: SyntaxNode, env: Env): number {
  return parseInt(env.source.slice(node.from, node.to), 10);
}

function readSubst(
  node: SyntaxNode,
  domain: Context,
  env: Env,
): { partialSubst: Array<SubstItemPattern | null>; patternVariables: Context } {
  const partialSubst = new Array<SubstItemPattern | null>(domain.length).fill(
    null,
  );
  const patternVariables: Context = [];
  node.getChildren("SubstItem").forEach((itemNode) => {
    const [lhsIx, rhs, itemPatternVariables] = readSubstItem(
      itemNode,
      domain,
      env,
    );
    if (partialSubst[lhsIx] != null) {
      throw new TypeInferenceError(
        `Duplicate substitution entry for '${domain[lhsIx].name}'`,
        itemNode,
      );
    }
    partialSubst[lhsIx] = rhs;
    patternVariables.push(...itemPatternVariables);
  });
  return { partialSubst, patternVariables };
}

function readSubstItem(
  node: SyntaxNode,
  domain: Context,
  env: Env,
): [Ix, SubstItemPattern, Context] {
  const [lhs, rhs = null] = node.getChildren("VariableName");
  const lhsName = readText(lhs, env);
  const lhsIx = domain.findIndex((item) => item.name === lhsName);
  if (lhsIx === -1) {
    throw new TypeInferenceError(
      `Undefined dependency '${lhsName}' in substitution`,
      lhs,
    );
  }

  const termNode = node.getChild("Term");
  if (rhs == null && termNode == null) {
    // Use the left-hand side variable name
    const rhsIx = env.context.findIndex((item) => item.name === lhsName);
    if (rhsIx === -1) {
      throw new TypeInferenceError(
        `Unbound variable '${lhsName}' in substitution`,
        lhs,
      );
    }
    if (env.context[rhsIx].level != domain[lhsIx].level) {
      throw new TypeInferenceError(
        `Variable '${lhsName}' at level ${env.context[rhsIx].level} cannot be used at level ${domain[lhsIx].level}`,
        lhs,
      );
    }
    return [lhsIx, rhsIx, []];
  }
  if (rhs != null) {
    // Use the right-hand side variable name
    const rhsName = readText(rhs, env);
    const rhsIx = env.context.findIndex((item) => item.name === rhsName);
    if (rhsIx === -1) {
      throw new TypeInferenceError(
        `Undefined dependency '${rhsName}' in substitution`,
        rhs,
      );
    }
    if (env.context[rhsIx].level != domain[lhsIx].level) {
      throw new TypeInferenceError(
        `Variable '${rhsName}' at level ${env.context[rhsIx].level} cannot be used at level ${domain[lhsIx].level}`,
        rhs,
      );
    }
    return [lhsIx, rhsIx, []];
  }
  // termNode != null
  const { term, type, patternVariables } = inferTerm(termNode!, {
    ...env,
    level: domain[lhsIx].level,
    context: domain[lhsIx].deps.concat(
      // Similarly, we do not filter the context here, as opposed to the original rule.
      env.context,
      // env.context.filter((item) => item.level >= domain[lhsIx].level),
    ),
    expectedType: domain[lhsIx].type,
  });
  if (!isEqualType(type, domain[lhsIx].type)) {
    throw new TypeInferenceError(
      `Type mismatch in substitution: expected ${JSON.stringify(domain[lhsIx].type)}, but got ${JSON.stringify(type)}`,
      termNode!,
    );
  }
  return [
    lhsIx,
    { term, deps: domain[lhsIx].deps, level: domain[lhsIx].level },
    patternVariables,
  ];
}

function readOptionalType(node: SyntaxNode | null, env: Env): Type | null {
  return node && readType(node, env);
}

function readType(node: SyntaxNode, env: Env): Type {
  switch (node.name) {
    case "BoolType":
      return { t: "bool" };
    case "IntType":
      return { t: "int" };
    case "CodeType": {
      const innerType = readType(node.getChild("Type")!, {
        ...env,
        level: env.level + 1,
      });
      return { t: "code", type: innerType };
    }
    case "CtxArrType": {
      const [argNode, retNode] = node.getChildren("Type");
      const arg = readType(argNode, env);
      const ret = readType(retNode, env);
      const depsNode = node.getChild("Context");
      const deps =
        readOptionalContext(depsNode, {
          ...env,
          level: env.level + 1,
        }) ?? [];
      return { t: "fun", arg, deps, ret };
    }
    case "WrapType": {
      const type = readType(node.getChild("Type")!, env);
      const deps =
        readOptionalContext(node.getChild("Context"), {
          ...env,
          level: env.level + 1,
        }) ?? [];
      return { t: "wrap", deps, type };
    }
    default:
      throw new TypeInferenceError(`Unknown type node: ${node.name}`, node);
  }
}
