import type {
  Term,
  Pattern,
  SubstItemPattern,
  Subst,
  SubstItem,
  Ix,
} from "./ast.ts";
import { printTerm, printSubst } from "./ast.ts";

type Rename = (ix: Ix) => Ix;

function ext(r: Rename, offset: number = 1): Rename {
  return (ix: Ix) => (ix < offset ? ix : r(ix - offset) + offset);
}

export function rename<T extends Pattern>(term: T, f: (ix: Ix) => Ix): T {
  switch (term.t) {
    case "patternVariable":
      return term; // Pattern variables are not renamed
    case "wildcard":
      return term; // Wildcards are not renamed
    case "varSubst":
      return { ...term, ix: f(term.ix), subst: renameSubst(term.subst, f) };
    case "true":
    case "false":
      return term;
    case "if":
      return {
        ...term,
        cond: rename(term.cond, f),
        then: rename(term.then, f),
        else: rename(term.else, f),
      };
    case "ctxAbs":
      return {
        ...term,
        body: rename(term.body, ext(f, 1)),
      };
    case "ctxApp":
      return {
        ...term,
        fun: rename(term.fun, f),
        arg: rename(term.arg, ext(f, term.deps.length)),
      };
    case "quote":
      // Since we do not remove current stage variables, in `inferQuoteTerm`,
      // we can safely rename the term with the same function. (TODO: can we?)
      return { ...term, term: rename(term.term, f) };
    case "letQuote":
      return {
        ...term,
        def: rename(term.def, ext(f, term.bind.deps.length)),
        body: rename(term.body, ext(f, 1)),
      };
    case "wrap":
      return { ...term, term: rename(term.term, ext(f, term.deps.length)) };
    case "letWrap":
      return {
        ...term,
        def: rename(term.def, f),
        body: rename(term.body, ext(f, 1)),
      };
    case "match":
      return {
        ...term,
        target: rename(term.target, f),
        branches: term.branches.map((branch) => ({
          ...branch,
          pattern: rename(branch.pattern, f),
          body: rename(branch.body, ext(f, branch.patternVariables.length)),
        })),
      };
    case "letRec":
      return {
        ...term,
        def: rename(term.def, ext(f, 1 + term.bind.deps.length)),
        body: rename(term.body, ext(f, 1)),
      };
    case "arith":
      return {
        ...term,
        left: rename(term.left, f),
        right: rename(term.right, f),
      };
    case "eq":
      return {
        ...term,
        left: rename(term.left, f),
        right: rename(term.right, f),
      };
    case "litInt":
      return term;
    case "rewrite":
      return {
        ...term,
        target: rename(term.target, f),
        pattern: rename(term.pattern, f),
        replacement: rename(
          term.replacement,
          ext(f, term.patternVariables.length),
        ),
      };
  }
}

export function renameSubst<T extends SubstItemPattern>(
  subst: T[],
  f: Rename,
): (T | number)[] {
  return subst.map((item) => {
    if (typeof item === "number") {
      return f(item);
    } else if (typeof item === "object") {
      return { ...item, term: rename(item.term, ext(f, item.deps.length)) };
    }
    return item satisfies never;
  });
}

export function ids(length: number): Subst {
  return Array.from({ length }, (_, i) => i);
}

export function exts<T extends SubstItemPattern>(
  subst: T[],
  offset: number,
): (T | number)[] {
  return (ids(offset) as (T | number)[]).concat(
    renameSubst(subst, (ix) => ix + offset),
  );
}

export function substitute<T extends Pattern>(
  term: T,
  subst: Subst,
  newContextLength: number,
): T | Term {
  switch (term.t) {
    case "patternVariable":
      return term; // Pattern variables are not substituted
    case "wildcard":
      return term; // Wildcards are not substituted

    case "varSubst":
      const item = subst[term.ix];
      const substSubst = substituteSubst(
        term.subst as Subst,
        subst,
        newContextLength,
      );
      if (typeof item === "number") {
        return { ...term, ix: item, subst: substSubst };
      } else {
        return substitute(
          item.term,
          substSubst.concat(ids(newContextLength)),
          newContextLength,
        );
      }
    case "true":
    case "false":
      return term;
    case "if":
      return {
        ...term,
        cond: substitute(term.cond, subst, newContextLength),
        then: substitute(term.then, subst, newContextLength),
        else: substitute(term.else, subst, newContextLength),
      };
    case "ctxAbs":
      return {
        ...term,
        body: substitute(term.body, exts(subst, 1), newContextLength + 1),
      };
    case "ctxApp":
      return {
        ...term,
        fun: substitute(term.fun, subst, newContextLength),
        arg: substitute(
          term.arg,
          exts(subst, term.deps.length),
          newContextLength + term.deps.length,
        ),
      };
    case "quote":
      return { ...term, term: substitute(term.term, subst, newContextLength) };
    case "letQuote":
      return {
        ...term,
        def: substitute(
          term.def,
          exts(subst, term.bind.deps.length),
          newContextLength + term.bind.deps.length,
        ),
        body: substitute(term.body, exts(subst, 1), newContextLength + 1),
      };
    case "wrap":
      return {
        ...term,
        term: substitute(
          term.term,
          exts(subst, term.deps.length),
          newContextLength + term.deps.length,
        ),
      };
    case "letWrap":
      return {
        ...term,
        def: substitute(term.def, subst, newContextLength),
        body: substitute(term.body, exts(subst, 1), newContextLength + 1),
      };
    case "match":
      return {
        ...term,
        target: substitute(term.target, subst, newContextLength),
        branches: term.branches.map((branch) => ({
          ...branch,
          pattern: substitute(branch.pattern, subst, newContextLength),
          body: substitute(
            branch.body,
            exts(subst, branch.patternVariables.length),
            newContextLength + branch.patternVariables.length,
          ),
        })),
      };
    case "letRec":
      return {
        ...term,
        def: substitute(
          term.def,
          exts(subst, 1 + term.bind.deps.length),
          newContextLength + 1 + term.bind.deps.length,
        ),
        body: substitute(term.body, exts(subst, 1), newContextLength + 1),
      };
    case "arith":
      return {
        ...term,
        left: substitute(term.left, subst, newContextLength),
        right: substitute(term.right, subst, newContextLength),
      };
    case "eq":
      return {
        ...term,
        left: substitute(term.left, subst, newContextLength),
        right: substitute(term.right, subst, newContextLength),
      };
    case "litInt":
      return term;
    case "rewrite":
      return {
        ...term,
        target: substitute(term.target, subst, newContextLength),
        pattern: substitute(term.pattern, subst, newContextLength),
        replacement: substitute(
          term.replacement,
          exts(subst, term.patternVariables.length),
          newContextLength + term.patternVariables.length,
        ),
      };
  }
}

export function substituteSubstItem<T extends SubstItemPattern>(
  item: T,
  subst: Subst,
  newContextLength: number,
): T | SubstItem {
  if (typeof item === "number") {
    return subst[item];
  } else if (typeof item === "object") {
    return {
      ...item,
      term: substitute(
        item.term,
        exts(subst, item.deps.length),
        newContextLength + item.deps.length,
      ),
    };
  }
  return item satisfies never;
}

export function substituteSubst<T extends SubstItemPattern>(
  subst1: T[],
  subst: Subst,
  newContextLength: number,
): (T | SubstItem)[] {
  return subst1.map((item) =>
    substituteSubstItem(item, subst, newContextLength),
  );
}

export function applyBottomUp(
  currentStage: number,
  contextLength: number,
  f: (contextLength: number, term: Term) => Term,
  term: Term,
): Term {
  switch (term.t) {
    case "varSubst":
      return {
        ...term,
        subst: applyBottomUpSubst(currentStage, contextLength, f, term.subst),
      };
    case "true":
    case "false":
      return f(contextLength, term);
    case "if":
      return f(contextLength, {
        ...term,
        cond: applyBottomUp(currentStage, contextLength, f, term.cond),
        then: applyBottomUp(currentStage, contextLength, f, term.then),
        else: applyBottomUp(currentStage, contextLength, f, term.else),
      });
    case "ctxAbs":
      return f(contextLength, {
        ...term,
        body: applyBottomUp(currentStage, contextLength + 1, f, term.body),
      });
    case "ctxApp":
      return f(contextLength, {
        ...term,
        fun: applyBottomUp(currentStage, contextLength, f, term.fun),
        arg: applyBottomUp(
          currentStage,
          contextLength + term.deps.length,
          f,
          term.arg,
        ),
      });
    case "quote":
      return f(contextLength, term);
    case "letQuote":
      return f(contextLength, {
        ...term,
        def: applyBottomUp(
          currentStage,
          contextLength + term.bind.deps.length,
          f,
          term.def,
        ),
        body: applyBottomUp(currentStage, contextLength + 1, f, term.body),
      });
    case "wrap":
      return f(contextLength, {
        ...term,
        term: applyBottomUp(
          currentStage,
          contextLength + term.deps.length,
          f,
          term.term,
        ),
      });
    case "letWrap":
      return f(contextLength, {
        ...term,
        def: applyBottomUp(currentStage, contextLength, f, term.def),
        body: applyBottomUp(currentStage, contextLength + 1, f, term.body),
      });
    case "match":
      return f(contextLength, {
        ...term,
        target: applyBottomUp(currentStage, contextLength, f, term.target),
        branches: term.branches.map((branch) => ({
          ...branch,
          body: applyBottomUp(
            currentStage,
            contextLength + branch.patternVariables.length,
            f,
            branch.body,
          ),
        })),
      });
    case "letRec":
      return f(contextLength, {
        ...term,
        def: applyBottomUp(
          currentStage,
          contextLength + 1 + term.bind.deps.length,
          f,
          term.def,
        ),
        body: applyBottomUp(currentStage, contextLength + 1, f, term.body),
      });
    case "arith":
      return f(contextLength, {
        ...term,
        left: applyBottomUp(currentStage, contextLength, f, term.left),
        right: applyBottomUp(currentStage, contextLength, f, term.right),
      });
    case "eq":
      return f(contextLength, {
        ...term,
        left: applyBottomUp(currentStage, contextLength, f, term.left),
        right: applyBottomUp(currentStage, contextLength, f, term.right),
      });
    case "litInt":
      return f(contextLength, term);
    case "rewrite":
      return f(contextLength, {
        ...term,
        target: applyBottomUp(currentStage, contextLength, f, term.target),
        replacement: applyBottomUp(
          currentStage,
          contextLength + term.patternVariables.length,
          f,
          term.replacement,
        ),
      });
  }
}

function applyBottomUpSubst(
  currentStage,
  contextLength,
  f: (contextLength: number, term: Term) => Term,
  subst: Subst,
): Subst {
  return subst.map((item) => {
    if (typeof item === "number") {
      return item;
    }
    if (item.level !== currentStage) {
      return item;
    }
    return {
      ...item,
      term: applyBottomUp(
        currentStage,
        contextLength + item.deps.length,
        f,
        item.term,
      ),
    };
  });
}
